// src/Scripts/TokenRefresh.js

const axios = require("axios"); // Импортируем axios
const router = require("@/router").default; // Импортируем Vue Router

/**
 * Планирует обновление токена доступа каждые 4 минуты
 */
function scheduleTokenRefresh() {
  const REFRESH_INTERVAL = 4 * 60 * 1000; // 4 минуты в миллисекундах
  
  console.log(`Планирование обновления токена через ${REFRESH_INTERVAL/1000} секунд`);
  
  setTimeout(() => {
    refreshAccessToken();
    scheduleTokenRefresh(); // Планируем следующее обновление
  }, REFRESH_INTERVAL);
}

/**
 * Обновляет токен доступа с использованием refreshToken.
 */
function refreshAccessToken() {
  const refreshToken = localStorage.getItem("refreshToken");
  if (refreshToken) {
    console.log("Попытка обновления токена доступа...");
    axios
      .post("api/accounts/token/refresh/", {
        refresh: refreshToken,
      })
      .then((response) => {
        const newAccessToken = response.data.access;
        if (newAccessToken) {
          localStorage.setItem("accessToken", newAccessToken);
          console.log("Токен доступа обновлён");
        } else {
          throw new Error("Новый accessToken отсутствует в ответе");
        }
      })
      .catch((error) => {
        console.error("Ошибка при обновлении токена доступа:", error);
        logout();
      });
  } else {
    console.error("Refresh Token отсутствует");
    logout();
  }
}

/**
 * Выполняет выход пользователя из системы.
 */
function logout() {
  // Удаляем все данные аутентификации из localStorage
  localStorage.removeItem("accessToken");
  localStorage.removeItem("refreshToken");
  localStorage.removeItem("user_id");

  // Перенаправляем пользователя на страницу входа или лэндинга
  router.push("/landing");
}

/**
 * Инициализирует автоматическое обновление токенов при запуске приложения.
 */
function initializeTokenRefresh() {
  scheduleTokenRefresh();
}

module.exports = {
  initializeTokenRefresh,
};

// **Добавьте следующие строки для глобального доступа (только для отладки)**
window.refreshAccessToken = refreshAccessToken;
window.scheduleTokenRefresh = scheduleTokenRefresh;

